@import "https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

body {
  min-height: 100vh;
  background-color: #6a5acd;
  justify-content: center;
  align-items: center;
  display: flex;
}

.container {
  width: 400px;
  height: 240px;
  text-align: center;
  color: #6a5acd;
  background-color: #fff;
  border-radius: 10px;
  place-content: center;
  gap: 30px;
  padding: 10px;
  display: grid;
}

form {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.input {
  width: 80%;
  height: 35px;
  border: 2px solid #6a5acd;
  border-radius: 10px;
  outline: none;
  margin-top: -10px;
  padding: 0 20px;
}

.input::placeholder {
  color: gray;
  opacity: .8;
}

.input:focus {
  box-shadow: 4px 4px 6px #0003;
}

.downloadBtn {
  width: 130px;
  height: 30px;
  cursor: pointer;
  pointer-events: none;
  color: #fff;
  opacity: .5;
  background-color: #6a5acd;
  border: none;
  border-radius: 10px;
  margin-top: 10px;
}

.downloadBtn:hover {
  filter: brightness(1.2);
}

.input:valid ~ .downloadBtn {
  opacity: 1;
  pointer-events: auto;
}

